.reviews-card {
  padding: 40px;
  background: $primary-bg;
  max-width: 390px;
  width: 100%;

  &:first-child {
    border-radius: 16px 0 0 16px;
  }

  &:last-child {
    border-radius: 0 16px 16px 0;
  }

  &--white {
    background: $white;
    border: none;
    box-shadow: 0px 4px 30px rgba(80, 106, 153, 0.1);
  }

  &__apostrophe {
    margin-bottom: 8px;
  }

  &__text {
    margin-bottom: 24px;
  }

  &__logotype {
    img {
      object-fit: contain;
      object-position: center;
      width: 175px;
      height: 64px;
    }
  }
}
