.s-capabilities-online {
  &__inner {
    padding: 40px 0 128px;
  }

  &__title {
    margin: 0 auto 16px;
    text-align: center;
    max-width: 356px;
    width: 100%;
  }

  &__text {
    max-width: 518px;
    width: 100%;
    margin: 0 auto;
    text-align: center;

    .link {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;

      color: $primary;
      text-decoration: underline;
    }
  }

  &__check-list {
    max-width: 425px;
    width: 100%;
  }

  &__content {
    display: flex;
    position: relative;
    gap: 50px;
    padding-top: 87px;
    margin-bottom: 73px;
  }

  &__img {
    width: 585px;
    line-height: 0;

    img {
      position: absolute;
      top: -30px;
      width: 585px;
      height: 535px;
      object-fit: cover;
      object-position: center;
    }
  }

  &__button {
    text-align: center;
    margin-top: 16px;
  }
}

@include mq(md) {
  .s-capabilities-online {
    &__inner {
      padding: 72px 16px 56px;
      align-items: center;
    }

    &__content {
      flex-direction: column;
      align-items: center;
      gap: 24px;
      padding: 32px 0;
      margin-bottom: 0;
    }

    &__button {
      margin: 0;
    }

    &__img {
      width: 100%;
      text-align: center;
      img {
        position: static;
        max-width: 343px;
        width: 100%;
        height: 314px;
      }
    }

    &__wrap {
      display: flex;
      flex-direction: column-reverse;
      gap: 16px;
    }


    .button {
      justify-content: center;
      max-width: 311px;
      width: 100%;
      background-color: $primary;
      color: $white;
    }
  }
}
